* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.buttons-field {
  height: 50vw;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
}

.button {
  cursor: pointer;
  min-width: 100px;
  min-height: 30px;
  text-align: center;
  background-color: #dd6767;
  border: none;
  border-radius: 10px;
  margin: 10px 20px;
  padding: 10px 15px;
  font-size: 20px;
}

.button.start {
  background-color: #64c75a;
}

.button:hover, .button:focus {
  background-color: rgba(161, 161, 161, .5);
}

.button:disabled {
  color: rgba(0, 0, 0, .5);
  cursor: not-allowed;
  background-color: rgba(102, 97, 97, .5);
}

